import React from 'react';
import './Dashboard_nav.css';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import Dashboard_nav from './Dashboard_nav';
import './delete_question.css';


class Delete_question extends React.Component {
    constructor(props) {
        super(props);
        //  this.handleLogin = this.handleLogin.bind(this)
        this.questions_id = this.questions_id.bind(this)
        this.state = {
            questions: [],
            question_ids: [],
        }
    }

    //http://localhost/form/show_questions.php

    handleLogin() {
        axios.get('./form/show_questions.php').then(res => this.setState({ questions: res.data }), () => { this.questions_id() })
    }


    questions_id() {
        let ids = []
        this.state.questions.map(question => {
            console.log("test")
            let id = question[1][0]
            ids.push(id)
        })

        this.setState({ question_ids: ids })
    }



    handleDelete(e) {
        let x = e.target.previousSibling._wrapperState.initialValue;

        axios.post('./form/delete_form.php', x).then(
            alert("تم مسح السؤال")
        )
    }


    componentDidMount() {
        this.handleLogin();
    }

    render() {
        let id = 0;
        return (
            <div>
                {this.state.questions.map(
                    question => {
                        return (
                            <form className="test_form">
                                <input class="form-control delete_input " type="label" value={question} />
                                <button onClick={this.handleDelete} type="button" class="btn btn-danger margin-question">إزاله السؤال</button>
                            </form>

                        )
                    }
                )}


            </div>
        )
    }

}


export default Delete_question;

