import React from 'react';
// import './applicant.css';
import Dashboard_nav from './Dashboard_nav';
import axios from 'axios';


class FAQ_edit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            question: '',
            answer: '',
            shown: [],
            q_s: []
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleDelete = this.handleDelete.bind(this)
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }


    // https://pollertest12.000webhostapp.com/form/add_FAQ.php
    // http://localhost/form/add_FAQ.php
    handleSubmit(e) {
        e.preventDefault();
        let x = [this.state.question, this.state.answer];
        axios.post('./form/add_FAQ.php', x).then( ()=> this.get_faq() ,function (response) { /// edit hena ll link
            console.log(response.config.data);
        });
        alert('تمت اضافه السؤال بنجاح')
    }


    /********** */
// https://pollertest12.000webhostapp.com/form/show_faq.php
// http://localhost/form/show_faq.php
    get_faq() {
        axios.get('./form/show_faq.php').then(res => this.setState({ q_s: res.data, check: 1 })
        )
    }


    set_shown() {
        let shown = [];
        shown.map(
            q => {
                shown.push(q)
            }
        )
        this.setState({ shown: shown })
    }



    handleDelete(e) {
        let x = e.target.previousSibling._wrapperState.initialValue
        console.log(x);


        // https://pollertest12.000webhostapp.com/form/delete_FAQ.php
        //http://localhost/form/delete_FAQ.php
        axios.post('./form/delete_FAQ.php', x).then(
            ()=> this.get_faq() , alert("تم مسح السؤال") , 
        )
    }


    componentDidMount() {
        this.get_faq();
    }

    /************ */



    render() {
        return (
            <div>
                <Dashboard_nav />

                <form className="pos_edit" onSubmit={this.handleSubmit} >
                    <div class="form-group">
                        <input class="form-control" name="question" placeholder="قم بكتابة السؤال هنا" onChange={this.handleChange} />
                    </div>
                    <div class="form-group">
                        <input class="form-control" name="answer" id="exampleInputPassword1" placeholder="قم بكتابة الإجابة هنا" onChange={this.handleChange} />
                    </div>
                    <button type="submit" class="btn btn-primary">إضافة سؤال</button>
                </form>

                <div>
                    {this.state.q_s.map(
                        question => {
                            return (
                                <form className="pos_edit">
                                    <input class="form-control delete_input " type="label" value={question.question} />
                                    <button onClick={this.handleDelete} type="button" class="btn btn-danger margin-question">إزاله السؤال</button>
                                </form>
                            )
                        }
                    )}
                </div>

            </div>
        )
    }
}



export default FAQ_edit;