import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Dashboard_nav from './Dashboard_nav';
import axios from 'axios';
import Delete_question from './delete_question';

let id_checkbox = 0;
let id_radio = 0;

class Formedit extends React.Component {

    constructor(props) {
        super(props);
        this.handleLogin = this.handleLogin.bind(this);
        this.Add_input_checkbox = this.Add_input_checkbox.bind(this);
        this.Add_radio_ans = this.Add_radio_ans.bind(this);
        this.Add_input_radio = this.Add_input_radio.bind(this);
        this.handleCheckSubmit = this.handleCheckSubmit.bind(this);
        this.handleInput = this.handleInput.bind(this);
        this.handle_q_1_ans = this.handle_q_1_ans.bind(this);
        this.handle_q_2 = this.handle_q_2.bind(this);
        this.Add_checkboc_ans = this.Add_checkboc_ans.bind(this);
        this.state = {
            my_form : <Delete_question /> ,
            input_field_checkbox: [],
            input_field_radio: [],
            input: false,
            checkbox: false,
            radio: false,
            checkBox_answer: [],
            radio_answer: [],
            form: [
                {
                    'type': '1',
                    'question': '',
                }
                ,
                {
                    'type': '2',
                    'question': '',
                    'answer': ['']
                }
                ,
                {
                    'type': '3',
                    'question': '',
                    'answer': ['']
                }
            ]
        }


    }

    handleCheckSubmit(e) {
        const item = e.target.name
        this.setState(prevState => ({
            [item]: !prevState[item],
        }))

    }


    handleInput(e) {
        let newvalue = e.target.value;
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '1');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['question'] = newvalue;
                return { mySites: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })
    }

    handle_q_1_ans(e) {
        let newvalue = e.target.value;
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '2');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['question'] = newvalue;
                return { mySites: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })
    }

    handle_q_2(e) {
        console.log(e.target.name);
        let newvalue = e.target.value;
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '3');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['question'] = newvalue;
                return { mySites: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })

    }


    andle_q_2_ans(e) {
        let newvalue = e.target.value;
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '3');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['question'] = newvalue;
                return { mySites: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })

    }

    set_ans_value(e) {
        let newvalue = e.target.value;
        this.setState(st => {
            return { name: newvalue }
        })
    }

    Add_input_checkbox(e) {
        let new_input = <input id={id_checkbox} onChange={this.Add_checkboc_ans} />;
        let arr = [...this.state.input_field_checkbox];
        arr[id_checkbox] = new_input;
        this.setState({
            input_field_checkbox: arr
        })

        id_checkbox++;
    }

    Add_input_radio(e) {
        let new_input = <input id={id_radio} onChange={this.Add_radio_ans} />;
        let arr = [...this.state.input_field_radio];
        arr[id_radio] = new_input;
        this.setState({
            input_field_radio: arr
        })

        id_radio++;
    }

    Add_checkboc_ans(e) {
        let newvalue = e.target.value;
        let my_id = e.target.id;
        if (my_id > 0) {
            newvalue = "/" + newvalue
        }
        let arr = [...this.state.checkBox_answer];
        arr[my_id] = newvalue;
        this.setState({ checkBox_answer: arr })
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '3');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['answer'] = arr;
                return { form: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })
    }

    Add_radio_ans(e) {
        let newvalue = e.target.value;
        let my_id = e.target.id;
        if (my_id > 0) {
            newvalue = "/" + newvalue
        }
        let arr = [...this.state.radio_answer];
        arr[my_id] = newvalue;
        this.setState({ radio_answer: arr })
        this.setState(prevState => {
            // find index of element
            const indexOfElement = prevState.form.findIndex(s => s['type'] === '2');
            if (indexOfElement > -1) {
                // if element exists copy the array...
                const sitesCopy = [...prevState.form];
                // ...and update the object
                sitesCopy[indexOfElement]['answer'] = arr;
                return { form: sitesCopy }
            }
            // there was no element with a given key so we don't update anything
        })
    }


    handleLogin(e) {
        e.preventDefault();
        let all_questions = this.state.form;
        let post_questions = all_questions.map(q => {
            if (q['question'] !== '') {
                return q;
            }
        });

        // https://pollertest12.000webhostapp.com/form/addform.php
        //http://localhost/form/addform.php 

        axios.post('./form/addform.php', post_questions).then(()=>this.set_shown() , function (response) { /// edit hena ll link
            console.log(response.config.data);
        });

        //        alert('تم ارسال البيانات بنجاح');

    }

    set_shown() {
        this.setState({
            shown : <Delete_question />
        })
    }

   
    render() {

        return (
            <div>
                <Dashboard_nav />


                <div class="form_page_edit">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg">
                                <h1 className="mt-3">ما الذي تريد ان تقوم بإضافته ؟</h1>
                                <div class="form-check checkk">
                                    <input class="form-check-input" type="checkbox" value="" id="input" name="input" onClick={this.handleCheckSubmit} />
                                    <label class="form-check-label" htmlFor="input" name="input"  >
                                        خانة فارغة
                                        <br />
                                        {this.state.input ? <input placeholder="ضع سؤالك هنا" onChange={this.handleInput} /> : null}
                                    </label>
                                    <br />
                                    <input class="form-check-input" type="checkbox" value="" id="radio" name="radio" onClick={this.handleCheckSubmit} />
                                    <label class="form-check-label" htmlFor="radio"  >
                                        سؤال متعدد الأجوبة
                                    <br />
                                        {
                                            this.state.radio ? <div>
                                                <input placeholder="ضع سؤالك هنا" onChange={this.handle_q_1_ans} />
                                                {this.state.input_field_radio}
                                                <button Style="color:black" onClick={this.Add_input_radio}>إضافة إجابة</button>
                                            </div> : null
                                        }

                                    </label>
                                    <br />

                                    <input class="form-check-input" type="checkbox" value="" id="checkbox" name="checkbox" onClick={this.handleCheckSubmit} />
                                    <label class="form-check-label" htmlFor="checkbox" >
                                        سؤال ذو إجابة واحدة
                                        <br />
                                        {
                                            this.state.checkbox ?
                                                <div>
                                                    <input placeholder="ضع سؤالك هنا" onChange={this.handle_q_2} />
                                                    <br />
                                                    {this.state.input_field_checkbox}
                                                    <button Style="color:black" onClick={this.Add_input_checkbox}>إضافة إجابة</button>
                                                </div> : null
                                        }
                                    </label>
                                    <br />
                                    <br />
                                    <button Style="color:black" onClick={this.handleLogin}>حفظ التغييرات</button>
                                </div>

                                {this.state.my_form}
                            </div>


                            <div className="col-lg form-pic" >
                                <div className="form-home">
                                    <img class="img-fluid phone" src="https://i.ibb.co/b6W8Vth/IOS.png" />
                                    <div className="form-t">
                                        <form onSubmit={this.handleClick}>
                                            <div id="loginform" className="form scrollbar scrollbar-track scrollbar-thumb" >
                                                <h1 className="head_form">قُم بملئ البيانات</h1>
                                                <div class="formmmm">
                                                    <small id="emailHelp" class="form-text text-muted">كن على علم أننا لن نشارك معلوماتك أبدًا</small>
                                                    <small id="emailHelp" class="form-text text-muted new_small">*يجب ملئ جميع البيانات حتى تنتقل للأسئلة القادمة</small>
                                                    {this.state.shown}
                                                    <div className="row">
                                                        <div class="col-11">
                                                            <button type="submit" className="btn btn-danger Next float-right">إرسال</button>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                
            </div>
        )
    }
}


export default Formedit;
