import React from 'react';
import './FAQ.css';
import Nav from './Nav';
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


class FAQ extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            q_s: '',
            check: 0,
        }
    }
    collapse = 0;


    // https://pollertest12.000webhostapp.com/form/show_faq.php
    //http://localhost/form/show_faq.php


    get_faq() {
        axios.get('./form/show_faq.php').then(res => this.setState({ q_s: res.data, check: 1 })
        )
    }

    componentDidMount() {
        this.get_faq();
    }

    render() {
        return (
            <div>
                <Nav />
                <div className="FAQ container">
                    <div id="accordion">
                        {
                            this.state.check == 1 ? this.state.q_s.map(qs => {
                                return (
                                    <div class="card ">
                                        <div class="card-header" id={qs.idq+1}>
                                            <h5 class="mb-0">
                                                <button class="btn btn-link" data-toggle="collapse" data-target={"#"+ "qs" +qs.idq} aria-expanded="false" aria-controls={qs.idq}>
                                                {qs.question}
                                        </button>
                                            </h5>
                                        </div>

                                        <div id={"qs" +qs.idq} class="collapse hide" aria-labelledby={qs.idq +1} data-parent="#accordion">
                                            <div class="card-body">
                                            {qs.answer}                                      </div>
                                        </div>
                                    </div>
                                )
                            })

                                : null
                        }

                    </div>
                </div>
            </div>
        )
    }

}



export default FAQ;
