import React from 'react';
import './login.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Show_applicants from './Show_applicants';
import { HashRouter as Router, Route } from 'react-router-dom';
import Formedit from './formedit';
import FAQ_edit from './FAQ_edit';


class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            un: '',
            pw: '',
            real_un: 'Fahad',
            real_pw: 'Baharain2020',
            shown: '',
            check_login: 0
        }
        this.handleLogin = this.handleLogin.bind(this);
        this.set_state = this.set_state.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    set_state() {
        let shown = <div className="container login">
            <img src="https://j.top4top.io/p_1717kr32s1.png" width="300" height="100" class="d-inline-block align-top" alt="" loading="lazy" />
            <div class="form-group">
                <label for="exampleInputEmail1" >UserName</label>
                <input name="un" type="email" onChange={this.handleChange} class="form-control my_c" id="exampleInputEmail1" aria-describedby="emailHelp" />
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1"  >Password</label>
                <input name="pw" type="password" onChange={this.handleChange} class="form-control my_c" id="exampleInputPassword1" />
            </div>
            <button type="submit" class="btn btn-primary" onClick={this.handleLogin}>Submit</button>
        </div>;
        this.setState({ shown: shown })
    }

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value })
    }

    handleLogin() {
        if (this.state.un == this.state.real_un && this.state.pw == this.state.real_pw) {
            let shown = <Show_applicants />
            this.setState({ shown: '', check_login: 55 })
            console.log("login sucess");


        } else {
            console.log("login failed")
        }
    }


    componentDidMount() {
        this.set_state();
    }

    render() {
        return (
            <div>
                {this.state.shown}
                {this.state.check_login == 55 ? <div>

                    <Show_applicants check={1} />
                </div> : null}
            </div>
        )
    }

}



export default Login;
