import React from 'react';
import './Dashboard_nav.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';


class Dashboard_nav extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            response: [],
            state_edited: false,
        }
        let res = [];

    }

   


    render() {

        let i = 0;
        return (
            <div >
                <header className="dashboard_nav">

                    <nav class="navbar navbar-expand-lg navbar-light bg-light">
                        <Link class="navbar-brand" to="/DashBoard">
                            <img src="https://j.top4top.io/p_1717kr32s1.png" width="150" height="30" class="d-inline-block align-top" alt="" loading="lazy" />
                        </Link>
                        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span class="navbar-toggler-icon"></span>
                        </button>
                        <div class="collapse navbar-collapse" id="navbarNav">
                            <ul class="navbar-nav" >

                                <li class="nav-item active" >
                                    <Link class="nav-link" to="/Dashboard" >Show applicants<span class="sr-only">(current)</span></Link>
                                </li>

                                <li class="nav-item">
                                    <Link class="nav-link" to="/Dashboard/FormEdit">Edit form</Link>
                                </li>

                                <li class="nav-item">
                                    <Link class="nav-link" to="/Dashboard/Edit_FAQ">Edit FAQ</Link>
                                </li>

                            </ul>
                        </div>
                    </nav>

                </header>
            </div>



        )
    }

}
/*


*/
export default Dashboard_nav;







