import React from 'react';
import './Nav.css';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';


class Nav extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }


    render() {
        return (
            <header>
            <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <Link class="navbar-brand" to="/">
                <img src="https://i.ibb.co/VVgBRcQ/POLLER.png" width="100" height="30" class="d-inline-block align-top" alt="" loading="lazy" />
            </Link>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
                <ul class="navbar-nav ul_nav">

                <li className="li_nav"><Link to="/Home">Home</Link></li>


                <li className="li_nav"><Link to="/Contact">Contact us</Link></li>


                <li className="li_nav"><Link to="/FAQ">FAQ</Link></li>


                </ul>
            </div>
        </nav>
            </header>
        );
    }
}

export default Nav;

