import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './form.css';
import Nav from './Nav';
import axios from 'axios';
import serializeForm from 'form-serialize';
import 'bootstrap/dist/js/bootstrap.min.js';

class Form extends React.Component {
    constructor(props) {

        super(props);

        this.answer_sperate = this.answer_sperate.bind(this)
        this.handleLogin = this.handleLogin.bind(this)
        this.set_form = this.set_form.bind(this)
        this.handleClick = this.handleClick.bind(this)
        //    this.handleLogin = this.handleLogin.bind(this);
        this.state = {
            form: [],
            shown: [],
            x: '',
            y: '',
            check: 0,
            refresh: 0
        }
    }

    //https://pollertest12.000webhostapp.com/form/get_form.php
    // http://localhost/form/get_form.php


    async handleLogin() {
        console.log("1 handle login")
        await axios.get('./form/get_form.php').then(res => this.setState({ form: res.data }, () => { this.answer_sperate() }, () => { this.set_form() })
        )
        console.log("2 handle login")
    }

    answer_sperate() {
        console.log("3 ans-seprate")

        this.setState(prevState => {
            // find index of element
            let i = 0;
            while (prevState.form[i]) {
                if (prevState.form[i]['type'] === '2' || prevState.form[i]['type'] === '3')
                    // const indexOfElement = prevState.form.findIndex(s => s['type'] === '2');
                    if (i > -1) {
                        console.log("from if staTEMENT")
                        // if element exists copy the array...
                        const sitesCopy = [...prevState.form];
                        // ...and update the object
                        let arr = sitesCopy[i]['answers'];
                        arr = arr.split("/")
                        console.log(arr)
                        sitesCopy[i]['answers'] = arr;
                        this.setState({ form: sitesCopy, check: 1 })
                    }
                // there was no element with a given key so we don't update anything
                i = i + 1;
            }
        }
        )

    }

    set_form() {
        console.log("4 set form")
        let form = this.state.form
        let id = 0;
        let name = 0;
        let shown = form.map(item => {
            return (
                <div>
                    <div>
                        {item.type == 1 ? <input class="form-control" name={id = id + 1} type="text" placeholder={item.question} /> : (item.type == 2) ? (<div>

                            <label className="ques" >{item.question}</label>
                            {item.answers.map(value => {
                                return (
                                    <div>
                                        <input type="checkbox" id={id = id + 1} name={item.question} value={value} />
                                        <label className="pl-2" for={id}>{value}</label>

                                    </div>
                                )
                            })
                            }

                        </div>) : (<div>
                            <label className="ques" id={name = name + 1}>{item.question}</label>
                            {item.answers.map(value => {
                                return (
                                    <div class="form-check checkk">
                                        <input class="form-check-input" type="radio" id={id = id + 1} name={item.question} value={value} />
                                        <label class="form-check-label" for={id} name={item.question} > {value}
                                        </label>
                                    </div>
                                )
                            })
                            }

                        </div>)}
                    </div>
                </div>
            )
        });
        this.setState({ shown: shown, check: 2 })

    }


    // http://localhost/form/index.php
    handleClick(e) {
        e.preventDefault();
        let formData = serializeForm(e.target, { hash: true });
        axios.post('./form/index.php', formData).then(function (response) { /// edit hena ll link
            console.log(response.config.data);
        });
        this.setState({ check: 0 })
        alert("تم إرسال البيانات بنجاح")
    }


    componentDidMount() {
        this.handleLogin();
        this.answer_sperate();
        this.set_form();
    }




    render() {

        let male = "إذا كنت تمتلك الخبرة في مجال العناية بالرجل (الحلاقة الرجالية) وترغب في زيادة دخلك من خلال العمل في وظيفة دوام جزئي أو دوام كامل .. بادر بتسجيل معلوماتك وسوف نقوم بالتواصل معك في أقرب فرصة ممكنة"
        let female = "إذا كنتِ تمتلكين الخبرة في مجال العناية بالمرة (المشاغل النسائية) وترغبين في زيادة دخلك من خلال العمل في وظيفة دوام جزئي أو دوام كامل .. بادري بتسجيل معلوماتك وسوف نقوم بالتواصل معكِ في أقرب فرصة ممكنة"
        let eng = "If you have enough experience in grooming services and looking for part-time or full-time job , please fill this form and we will contact you ASAP"
        return (
            <div class="form_page">
                {        this.state.check == 1 ? this.set_form() : null
                }
                {this.state.refresh == 1 ? this.forceUpdate() : null}
                <Nav />
                <div className="container">
                    <div className="row">
                        <div className="col-lg">
                            <img className="img-fluid text-center" src="https://i.ibb.co/VVgBRcQ/POLLER.png" />
                            <h1>توظيف كوافيرات وحلاقين</h1>
                            <p className="gender_p">{male}</p>
                            <p>{eng}</p>
                        </div>


                        <div className="col-lg form-pic" >
                            <div className="form-home">
                                <img class="img-fluid phone" src="https://i.ibb.co/b6W8Vth/IOS.png" />
                                <div className="form-t">
                                    <form onSubmit={this.handleClick}>
                                        <div id="loginform" className="form scrollbar scrollbar-track scrollbar-thumb" >
                                            <h1 className="head_form">قُم بملئ البيانات</h1>
                                            <div class="formmmm">
                                                <small id="emailHelp" class="form-text text-muted">كن على علم أننا لن نشارك معلوماتك أبدًا</small>
                                                <small id="emailHelp" class="form-text text-muted new_small">*يجب ملئ جميع البيانات حتى تنتقل للأسئلة القادمة</small>
                                                {this.state.shown}
                                                <div className="row">
                                                    <div class="col-11">
                                                        <button type="submit" className="btn btn-danger Next float-right">إرسال</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}

export default Form;