import React from 'react';
import './Show_applicants.css';
import axios from 'axios';
// import Applicant from './applicant';
import 'bootstrap/dist/css/bootstrap.min.css';
import Dashboard_nav from './Dashboard_nav';
import { HashRouter as Router, Route } from 'react-router-dom';
import Formedit from './formedit';
import FAQ_edit from './FAQ_edit';


class Show_applicants extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            response: [],
            state_edited: false,
        }
        this.get_data = this.get_data.bind(this);
        let res = [];

    }


    //http://localhost/form/get.php
    get_data() {
        console.log("tessssssssssssssst");
        axios.get('./form/get.php').then(res => this.setState({ response: res.data }))
    }



    componentDidMount() {

        this.get_data();
        console.log(this.state.response)

    }

    render() {
        console.log(this.state.response)

        let i = 0;
        return (
            <div>
                <Dashboard_nav />

                <div className="row no-gutters style_div">
                    {this.state.response.map(element => {
                        return (
                            <div className="col-lg-4 box">
                                <ul className="ul_dash">
                                    <li className="li_dash">{Object.keys(JSON.parse(element.USER)).map(function (key) {
                                        return (<li>{JSON.parse(element.USER)[key]}</li>)
                                    })}</li>
                                </ul>
                            </div>
                        )
                    })}
                </div>

            </div>
        )
    }

}


export default Show_applicants;
